import { CommonModule } from '@angular/common';
import { Component, computed, input, output } from '@angular/core';
import { FormControl, ReactiveFormsModule } from '@angular/forms';
import { debounceTime, map, startWith } from 'rxjs';
import { AuthUserResponse } from '../../../api';
import { IconField } from 'primeng/iconfield';
import { InputTextModule } from 'primeng/inputtext';
import { toSignal } from '@angular/core/rxjs-interop';
import { InputIcon } from 'primeng/inputicon';

@Component({
  selector: 'app-client-selector',
  templateUrl: './client-selector.component.html',
  styleUrls: ['./client-selector.component.scss'],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    IconField,
    InputTextModule,
    InputIcon,
  ],
})
export class ClientSelectorComponent {
  clientAccounts = input<AuthUserResponse[]>([]);
  maxHeightClass = input<string>('max-h-96');
  accountSelected = output<AuthUserResponse>();
  searchControl = new FormControl<string>('');
  filteredAccounts = computed(() => {
    const term = this.searchTerm().toLowerCase().trim();
    return this.clientAccounts().filter(
      (account) =>
        account.name.toLowerCase().includes(term) ||
        (account?.owner?.name ?? '').toLowerCase().includes(term) ||
        (account?.owner?.type ?? '').toLowerCase().includes(term),
    );
  });
  private readonly searchTerm = toSignal(
    this.searchControl.valueChanges.pipe(
      startWith(''),
      debounceTime(300),
      map((value) => value ?? ''),
    ),
    { initialValue: '' },
  );

  selectAccount(accountId: string): void {
    const selected = this.clientAccounts().find(
      (account) => account.id === accountId,
    );
    if (selected) {
      this.accountSelected.emit(selected);
    }
  }
}
