import { inject, Injectable, signal, computed, effect } from '@angular/core';
import { LocalStorageService } from './local-storage.service';
import {
  LOCAL_STORAGE_KEY_RECENT_CLIENTS,
  MAX_RECENT_CLIENTS,
} from '../../core/models';
import { isArray, isDefined, isNullOrUndefined } from '../utils';

/**
 * Represents a recently selected client stored in localStorage.
 * Contains minimal data needed for display and selection.
 */
export interface RecentClient {
  /** Unique client identifier */
  id: string;
  /** Client display name */
  name: string;
  /** Timestamp when client was last selected */
  lastSelectedAt: number;
}

/**
 * Service for managing recently selected clients in localStorage.
 * Provides functionality to track, retrieve, and manage the user's
 * most recently selected clients for quick access in the Context Selection dialog.
 *
 * Features:
 * - Stores up to MAX_RECENT_CLIENTS (default 5) recent clients
 * - Prevents duplicate entries (re-selecting moves client to top)
 * - Persists across browser sessions via localStorage
 * - Graceful fallback when localStorage is unavailable
 * - User-scoped storage (each user has their own recent clients list)
 */
@Injectable({
  providedIn: 'root',
})
export class RecentClientsService {
  private readonly localStorageService = inject(LocalStorageService);

  /** Signal containing the current list of recent clients */
  private readonly recentClientsSignal = signal<RecentClient[]>([]);

  /** Computed signal exposing the recent clients list as readonly */
  readonly recentClients = computed(() => this.recentClientsSignal());

  constructor() {
    // React to user/API namespace changes
    effect(() => {
      const userId = this.localStorageService.getCurrentUserId();
      if (isNullOrUndefined(userId)) {
        this.recentClientsSignal.set([]);
        return;
      }
      // Migrate any legacy non-namespaced value into the correct namespace
      this.localStorageService.migrateToNamespace(
        LOCAL_STORAGE_KEY_RECENT_CLIENTS,
        true,
      );
      // Prune incorrect API-only or pending variants to avoid cross-pollution
      this.localStorageService.pruneIncorrectRecentClients();

      this.loadRecentClients();
    });
  }

  /**
   * Loads recent clients from localStorage into the signal.
   * Called on service initialization and can be called to refresh from storage.
   */
  loadRecentClients(): void {
    try {
      const stored = this.localStorageService.getItem<RecentClient[]>(
        LOCAL_STORAGE_KEY_RECENT_CLIENTS,
      );

      if (isDefined(stored) && isArray<RecentClient>(stored)) {
        // Validate and filter the stored data
        const validClients = stored.filter(
          (client) =>
            isDefined(client) &&
            isDefined(client.id) &&
            isDefined(client.name) &&
            typeof client.id === 'string' &&
            typeof client.name === 'string',
        );
        this.recentClientsSignal.set(validClients);
      } else {
        this.recentClientsSignal.set([]);
      }
    } catch (error) {
      console.warn('Failed to load recent clients from localStorage:', error);
      this.recentClientsSignal.set([]);
    }
  }

  /**
   * Adds a client to the recently used list.
   * If the client already exists, it moves to the top of the list.
   * Maintains a maximum of MAX_RECENT_CLIENTS entries.
   *
   * @param clientId - The unique identifier of the client
   * @param clientName - The display name of the client
   */
  addRecentClient(clientId: string, clientName: string): void {
    if (!clientId || !clientName) {
      console.warn('Cannot add recent client: missing id or name');
      return;
    }

    try {
      const currentClients = [...this.recentClientsSignal()];

      // Remove existing entry if present (to move it to top)
      const existingIndex = currentClients.findIndex(
        (client) => client.id === clientId,
      );
      if (existingIndex !== -1) {
        currentClients.splice(existingIndex, 1);
      }

      // Create new entry with current timestamp
      const newClient: RecentClient = {
        id: clientId,
        name: clientName,
        lastSelectedAt: Date.now(),
      };

      // Add to beginning of list
      currentClients.unshift(newClient);

      // Trim to max size
      const trimmedClients = currentClients.slice(0, MAX_RECENT_CLIENTS);

      // Update signal and persist to storage
      this.recentClientsSignal.set(trimmedClients);
      this.saveToStorage(trimmedClients);
    } catch (error) {
      console.warn('Failed to add recent client:', error);
    }
  }

  /**
   * Removes a specific client from the recently used list.
   *
   * @param clientId - The unique identifier of the client to remove
   */
  removeRecentClient(clientId: string): void {
    if (!clientId) return;

    try {
      const currentClients = this.recentClientsSignal();
      const filteredClients = currentClients.filter(
        (client) => client.id !== clientId,
      );

      if (filteredClients.length !== currentClients.length) {
        this.recentClientsSignal.set(filteredClients);
        this.saveToStorage(filteredClients);
      }
    } catch (error) {
      console.warn('Failed to remove recent client:', error);
    }
  }

  /**
   * Clears all recent clients from the list and storage.
   */
  clearRecentClients(): void {
    try {
      this.recentClientsSignal.set([]);
      this.localStorageService.removeItem(LOCAL_STORAGE_KEY_RECENT_CLIENTS);
    } catch (error) {
      console.warn('Failed to clear recent clients:', error);
    }
  }

  /**
   * Checks if a client is in the recent clients list.
   *
   * @param clientId - The unique identifier of the client to check
   * @returns True if the client is in the recent list
   */
  isRecentClient(clientId: string): boolean {
    return this.recentClientsSignal().some((client) => client.id === clientId);
  }

  /**
   * Gets the current count of recent clients.
   *
   * @returns The number of recent clients stored
   */
  getRecentClientsCount(): number {
    return this.recentClientsSignal().length;
  }

  /**
   * Persists the recent clients list to localStorage.
   *
   * @param clients - The list of recent clients to save
   */
  private saveToStorage(clients: RecentClient[]): void {
    try {
      this.localStorageService.setItem(
        LOCAL_STORAGE_KEY_RECENT_CLIENTS,
        clients,
      );
    } catch (error) {
      console.warn('Failed to save recent clients to localStorage:', error);
      // Continue operation - the in-memory signal is still updated
    }
  }
}
