import {
  Component,
  computed,
  signal,
  output,
  input,
  DestroyRef,
  inject,
  ElementRef,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { InputTextModule } from 'primeng/inputtext';
import { InputIcon } from 'primeng/inputicon';
import { IconField } from 'primeng/iconfield';
import { TooltipModule } from 'primeng/tooltip';
import { PaginatorModule, PaginatorState } from 'primeng/paginator';
import { TableLazyLoadEvent } from 'primeng/table';
import { debounceTime, Subject } from 'rxjs';
import { ClientListItem } from '../../../api';
import { isEmpty, isDefined } from '../../utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { RecentClient } from '../../services/recent-clients.service';
import { COLORS, DEFAULT_PAGE_SIZE } from '../../../core/models/constants';
import { ApiUrlService } from '../../../core/services/api-url.service';

@Component({
  selector: 'app-client-selection-form',
  imports: [
    FormsModule,
    InputTextModule,
    InputIcon,
    IconField,
    TooltipModule,
    PaginatorModule,
  ],
  templateUrl: './client-selection-form.component.html',
  styleUrl: './client-selection-form.component.scss',
})
export class ClientSelectionFormComponent {
  readonly clients = input<ClientListItem[]>([]);
  readonly loading = input<boolean>(false);
  // Whether to use server-side search (true if total clients >= 100)
  readonly useServerSearch = input<boolean>(false);
  readonly totalRecords = input<number>(0);
  readonly enablePagination = input<boolean>(true);
  readonly enableLazyLoad = input<boolean>(false);
  /** Recent clients to display at the top of the list */
  readonly recentClients = input<RecentClient[]>([]);

  readonly clientSelected = output<string>();
  readonly searchQuery = output<string>();
  readonly lazyLoad = output<TableLazyLoadEvent>();

  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject(ElementRef);
  private readonly apiUrlService = inject(ApiUrlService);

  // Search state
  search = signal<string>('');
  private readonly searchChanges$ = new Subject<string>();

  // Keyboard navigation state
  readonly focusedIndex = signal<number>(-1);

  // Logo loading state
  readonly failedLogos = signal<Record<string, boolean>>({});

  // Pagination state
  readonly first = signal<number>(0);
  readonly rows = signal<number>(DEFAULT_PAGE_SIZE);

  // Computed trimmed term for all filtering logic
  private readonly searchTerm = computed(() => this.search().trim());

  /** Total number of navigable items (recent clients chips + items on current page) */
  private readonly totalNavigableItems = computed(() => {
    const recentCount = this.showRecentSection()
      ? this.recentClients().length
      : 0;
    const clientCount = this.displayedClients().length;
    return recentCount + clientCount;
  });

  /** Whether to show the recently used section */
  readonly showRecentSection = computed(() => {
    const recent = this.recentClients();
    const term = this.searchTerm();
    // Show recent section only when there are recent clients and no search is active
    return recent.length > 0 && isEmpty(term);
  });

  /** Filtered recent clients based on search term (for when search is active) */
  readonly filteredRecentClients = computed(() => {
    const recent = this.recentClients();
    const term = this.searchTerm().toLowerCase();

    if (isEmpty(term)) return recent;
    return recent.filter((c) => c.name.toLowerCase().includes(term));
  });

  filteredClients = computed(() => {
    if (this.useServerSearch()) {
      return this.clients();
    }

    const term = this.searchTerm().toLowerCase();
    const clients = isEmpty(term)
      ? this.clients()
      : this.clients().filter((c) => c.name.toLowerCase().includes(term));

    return clients;
  });

  /** Clients to actually display on the current page */
  readonly displayedClients = computed(() => {
    const clients = this.filteredClients();
    if (!this.enablePagination() || this.useServerSearch()) {
      return clients;
    }

    const start = this.first();
    const end = start + this.rows();
    return clients.slice(start, end);
  });

  /** Total records for the paginator */
  readonly currentTotalRecords = computed(() => {
    if (this.useServerSearch()) {
      return this.totalRecords();
    }
    return this.filteredClients().length;
  });

  constructor() {
    this.searchChanges$
      .pipe(debounceTime(300), takeUntilDestroyed(this.destroyRef))
      .subscribe((term) => {
        if (this.useServerSearch()) {
          this.searchQuery.emit(term);
        }
      });
  }

  onSearchInput(): void {
    const term = this.searchTerm();
    this.searchChanges$.next(term);
    // Reset focus and pagination when search changes
    this.focusedIndex.set(-1);
    this.first.set(0);
  }

  onPageChange(event: PaginatorState): void {
    if (isDefined(event.first)) this.first.set(event.first);
    if (isDefined(event.rows)) this.rows.set(event.rows);

    if (this.useServerSearch() && this.enableLazyLoad()) {
      this.onLazyLoad({
        first: event.first,
        rows: event.rows,
      });
    }

    // Reset focus when page changes
    this.focusedIndex.set(-1);
  }

  selectClient(clientId: string | undefined): void {
    if (isDefined(clientId) && clientId !== '') {
      this.clientSelected.emit(clientId);
    }
  }

  handleLogoError(clientId: string | undefined): void {
    if (isDefined(clientId) && clientId !== '') {
      this.failedLogos.update((prev) => ({ ...prev, [clientId]: true }));
    }
  }

  onLazyLoad(event: TableLazyLoadEvent): void {
    this.lazyLoad.emit(event);
  }

  /**
   * Handle keyboard events for grid navigation
   * Supports arrow keys for 2D navigation, Home/End for first/last item
   */
  onKeyDown(event: KeyboardEvent): void {
    const target = event.target as HTMLElement;
    const isInput = target.tagName === 'INPUT';
    const totalItems = this.totalNavigableItems();

    if (totalItems === 0 && !isInput) return;

    const currentIndex = this.focusedIndex();

    // Define keys that should be handled by the component for navigation
    const navigationKeys = [
      'ArrowDown',
      'ArrowUp',
      'ArrowRight',
      'ArrowLeft',
      'Home',
      'End',
      'Enter',
      ' ',
    ];

    if (!navigationKeys.includes(event.key)) return;

    // Special handling for input field to allow normal typing
    if (isInput) {
      // Allow space and cursor movement keys in input without interference
      if ([' ', 'ArrowLeft', 'ArrowRight', 'Home', 'End'].includes(event.key)) {
        return;
      }

      // Special case: Enter in input selects the first result if nothing is focused
      if (event.key === 'Enter' && currentIndex < 0 && totalItems > 0) {
        event.preventDefault();
        this.selectItemAtIndex(0);
        return;
      }
    }

    const actions: Record<string, () => number> = {
      ArrowDown: () => this.calculateNextIndexDown(currentIndex, totalItems),
      ArrowUp: () => this.calculateNextIndexUp(currentIndex),
      ArrowRight: () =>
        currentIndex < totalItems - 1 ? currentIndex + 1 : currentIndex,
      ArrowLeft: () => (currentIndex > 0 ? currentIndex - 1 : 0),
      Home: () => 0,
      End: () => totalItems - 1,
      Enter: () => {
        if (currentIndex >= 0) this.selectItemAtIndex(currentIndex);
        return currentIndex;
      },
      ' ': () => {
        if (currentIndex >= 0) this.selectItemAtIndex(currentIndex);
        return currentIndex;
      },
    };

    const action = actions[event.key];
    if (typeof action === 'function') {
      event.preventDefault();
      const newIndex = action();
      if (newIndex !== currentIndex && newIndex >= 0) {
        this.focusedIndex.set(newIndex);
        this.focusItemAtIndex(newIndex);
      }
    }
  }

  private calculateNextIndexDown(current: number, total: number): number {
    const isMobile = window.innerWidth < 640;
    const columnsCount = isMobile ? 1 : 2;
    const recentCount = this.showRecentSection()
      ? this.recentClients().length
      : 0;

    if (current < 0) return 0;
    if (current < recentCount) return recentCount;
    return Math.min(current + columnsCount, total - 1);
  }

  private calculateNextIndexUp(current: number): number {
    const isMobile = window.innerWidth < 640;
    const columnsCount = isMobile ? 1 : 2;
    const recentCount = this.showRecentSection()
      ? this.recentClients().length
      : 0;

    const firstClientRow =
      current >= recentCount && current < recentCount + columnsCount;
    if (firstClientRow) {
      return recentCount > 0 ? recentCount - 1 : current;
    }
    if (current >= recentCount) {
      return Math.max(current - columnsCount, recentCount);
    }
    return current;
  }

  /**
   * Track focus on individual items for roving tabindex pattern
   */
  onItemFocus(index: number): void {
    this.focusedIndex.set(index);
  }

  /**
   * Get the tabindex for an item based on roving tabindex pattern
   * Only one item should be tabbable at a time
   */
  getItemTabIndex(index: number): number {
    const focused = this.focusedIndex();
    // If nothing is focused yet, make first item tabbable
    if (focused < 0 && index === 0) return 0;
    // Otherwise, only focused item is tabbable
    return focused === index ? 0 : -1;
  }

  /**
   * Check if an item is currently focused for styling
   */
  isItemFocused(index: number): boolean {
    return this.focusedIndex() === index;
  }

  /**
   * Calculate the global index for a recent client chip
   */
  getRecentClientIndex(localIndex: number): number {
    return localIndex;
  }

  /**
   * Calculate the global index for a client card (relative to items on current page)
   */
  getClientCardIndex(localIndex: number): number {
    const recentCount = this.showRecentSection()
      ? this.recentClients().length
      : 0;
    return recentCount + localIndex;
  }

  /** Focus the DOM element at the given index */
  private focusItemAtIndex(index: number): void {
    const recentCount = this.showRecentSection()
      ? this.recentClients().length
      : 0;

    let selector: string;
    let itemIndex: number;

    if (index < recentCount) {
      // Focus recent client chip
      selector = '.recent-client-chip';
      itemIndex = index;
    } else {
      // Focus client card
      selector = '.client-card';
      itemIndex = index - recentCount;
    }

    const elements = this.elementRef.nativeElement.querySelectorAll(selector);
    const element = elements[itemIndex] as HTMLElement | undefined;
    if (element) {
      element.focus();
    }
  }

  /** Select the item at the given global index */
  private selectItemAtIndex(index: number): void {
    const recentCount = this.showRecentSection()
      ? this.recentClients().length
      : 0;

    if (index < recentCount) {
      const recentClients = this.recentClients();
      const client = recentClients[index];
      if (client?.id) {
        this.selectClient(client.id);
      }
    } else {
      const clients = this.displayedClients();
      const clientIndex = index - recentCount;
      const client = clients[clientIndex];
      if (client?.id) {
        this.selectClient(client.id);
      }
    }
  }

  /**
   * Get a consistent theme color for a client name
   */
  getClientColor(name: string): string {
    if (isEmpty(name)) return COLORS[0];
    let hash = 0;
    for (const char of name) {
      const charCode = char.codePointAt(0);
      if (charCode !== undefined) {
        hash = charCode + ((hash << 5) - hash);
      }
    }
    const index = Math.abs(hash) % COLORS.length;
    return COLORS[index];
  }

  /**
   * Get the logo URL for a client
   */
  getClientLogoUrl(clientId: string | undefined): string {
    if (isEmpty(clientId)) return '';
    return `${this.apiUrlService.getCurrentApiUrl()}/accounts/clients/${clientId}/logo?size=small`;
  }
}
